* {
  box-sizing: border-box;
}

body {
  text-align: center;
  display: flex;
 /* height: 25vh;*/
  justify-content: center;
  align-items: center;
  padding: 0 4vw;
  
}
.ta {
  border-radius: 50%;
  
  height: 12vh;
  left: 0;
  align-self: start;
  z-index: 1;
  position: fixed;
}

.nav0{
  top: 0.2rem;
  position: fixed;
}

.nav {
  position: relative;
  align-items: center;
  
  justify-content: center;
  display: inline-flex;
  
  overflow: hidden;
  width: 98vw;
  height: 12vh;
  background-color: #fff;
  border-radius: 50px;
  box-shadow: 0 10px 40px rgba(159, 162, 177, .8);
  
}

.nav-item {
  left: 0rem;
  color: #83818c;
  padding: 4vw;
  text-decoration: none;
  transition: .3s;
  margin: 0 1px;
  z-index: 1;
  font-family: 'DM Sans', sans-serif;
  font-weight:700;
  position: relative;
  font-size: 2vw ;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  
  
}

.nav-item:before {
  content: "";
  position: absolute;
  bottom: -6vh;
  left: 0;
  width: 100%;
  height: 5vh;
  background-color: #7acef5c5;
  border-radius: 8px 8px 0 0;
  opacity: 0;
  transition: .3s;
}

.nav-item:not(.is-active):hover:before {
  opacity: 1;
  bottom: 0;
}


.nav-item:not(.is-active):hover { color: #333; }

.nav-indicator {
  position: absolute;
  
  bottom: 0;
 
  transition: .4s;
  height: 5px;
  z-index: 1;
  border-radius: 8px 8px 0 0;
}

@media (max-width: 580px) {
  .nav { overflow: auto; }
}

@media only screen and (max-width: 800px) {

  .nav-item
  {
    font-size: 3vw;
  }
  .nav , .ta{
    height: 9vh;
  }

}



